
/* GlobaL sTyle */
.AddItem {
    display: flex;
    flex-direction: column;
    width: 450px;
    margin: auto;
    margin-top: 100px;
}

/* Inputs Place */
.AddItem > * {
    padding-top: 10px;
    padding-bottom: 10px;
}


/* CheckBox Div Style */
.ReadOnly {
    display: inline-flex;
    max-height: fit-content;
    max-width: fit-content;
}

/* CheckBox Text Style */
.ReadOnly > p {
    padding: 2px;
    margin: 0;
    border: none;
}


/* CheckBox Style */
.ReadOnly > input[type="checkbox"] {
    position: relative;
    background: rgb(255, 255, 255);
    top: 4px;
    width: 10px;
    height: 10px;
    -webkit-appearance: initial;
    border-radius: 3px;
    cursor: pointer;
    border: 0.5px rgb(0, 255, 200) solid;
}

.ReadOnly > input[type="checkbox"]:after {
    position: relative;
    top: 15%;
    left : 15%;
    content: "";
    display: block;
    line-height: 32px;
    width: 70%;
    height: 70%;
    box-sizing: border-box;
    text-align: center;
    border-radius: 10px;
  }
  
  .ReadOnly > input[type="checkbox"]:checked:after {
    background: rgb(0, 255, 200);
    
  }

  .ReadOnly > input[type="checkbox"]:hover {
    background: rgb(210, 224, 223);
  }