.App{

}

h1 {
    font-size: 70px;
    font-weight: bold;
    color: rgb(255, 255, 255);
    text-shadow: 2px 2px rgb(0, 0, 0);
    text-align: center;
    margin-top: 20px;
    margin-bottom: 20px;
}

details {
    position: relative;
    text-align: center;
    max-width: 500px;
    box-sizing: border-box;
    margin-top: 20px;
    margin-bottom: 20px;
    padding: 10px;
    background-color: aliceblue;
    border-radius: 5px;
    left: 50%;
    transform: translateX(-50%);
}

.gitLink {
    position: fixed;
    display: flex;
    top: 97%;
    left: 1%;
}

.gitLink > p {
    margin-right: 5px;
}