.ItemComp {
  display: flex;
  flex-direction: column;
  margin: 0px 20px;
  margin-bottom: 10px;
  background-color: #e8eaf6;
  width: 90%;
  border-radius: 10px;
}

.ItemComp:last-child {
  margin-bottom: 0;
}

/* delete image border */
.ItemComp > .ItemComp-img {
  margin: auto auto -4px auto;
}
/* make all images same size */
.ItemComp > .ItemComp-img > img {
  width: 200px;
  height: 200px;
}

.ItemComp > .ItemComp-info {
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
}

.ItemComp > .ItemComp-info > .ItemComp-text > *{
  margin-bottom: 20px;
  margin-right: 20px;
}

.ItemComp > .ItemComp-info > .ItemComp-text > *:last-child{
  margin-bottom: 0px;
}

.ItemComp > .ItemComp-info {
  display: flex;
  justify-content: center;
  color: black;
  background-color: aliceblue;
  padding: 20px;
}
/* center the p text */
.ItemComp > .ItemComp-info > .ItemComp-price{
  display: flex;
  justify-content: center;
  align-items: center;
}

