/* Center the div and display vertical scrollbar */
.ItemList {
  position: relative;
  display: flex;
  flex-direction: column;
  max-width: 40%;
  left: 50%;
  transform: translateX(-50%);
  overflow-y: scroll;
  height: 81vh;
}
/* style the scrollbar */
.ItemList::-webkit-scrollbar {
  width: 10px;
}
/* style the scrollbar thumb */
.ItemList::-webkit-scrollbar-thumb {
  background: #5c6bc0;/*rgb(150, 158, 165);*/
  border-radius: 10px;
}
/* style the scrollbar button */
/*
.ItemList::-webkit-scrollbar-button {
  background: rgb(0, 168, 219);
  border-radius: 10px;
}
*/
/* style the scrollbar track */
.ItemList::-webkit-scrollbar-track {
  background: aliceblue;
  border-radius: 10px;
}
/* style the scrollbar track */
.ItemList::-webkit-scrollbar-track-piece {
  background: #e8eaf6;
  border-radius: 10px;
}




